<template>
    <view class="container">
        <view class="identity-section">
            <text class="label">当前身份:</text>
            <text class="id">{{ identity }}</text>
        </view>

        <!-- 当用户不是买家时显示成为买家的按钮 -->
        <button class='btn switch-btn' v-if="identityCode !== '1'" @click="switchToBuyer">切换为买家</button>

        <!-- 当用户不是业主时显示成为业主的按钮 -->
        <button class='btn switch-btn' v-if="identityCode !== '5'" @click="switchToOwner">切换为业主</button>

        <!-- 当用户不是员工时显示成为员工的按钮 -->
        <button class='btn switch-btn' v-if="identityCode !== '4'" @click="switchToEmployee">切换为员工</button>

        <button class='btn register-btn' @click="direct">注册</button>
    </view>
</template>

<script>
export default {
    data() {
        return {
            userId: uni.getStorageSync('name'),
            identityCode: uni.getStorageSync('id'),
            address: ''
        };
    },
    computed: {
        identity() {
            switch (this.identityCode) {
                case '1': return '买家';
                case '5': return '业主';
                case '4': return '员工';
                default: return '未知';
            }
        }
    },
    methods: {
        direct() {
            uni.navigateTo({
                url: '/subPackageUser/pages/register/register'
            });
        },
        switchIdentity(targetId) {
            uni.request({
                url: 'https://api.mdpszu.ltd:8090/api/user/changeid',
                method: 'POST',
                data: {
                    login_state_string: uni.getStorageSync('lss'),
                    idToSwitch: targetId
                },
                success: res => {					
					this.setTabBarForIdentity(idToSwitch);//更改导航栏
                    if (res.statusCode === 200) {
						uni.setStorageSync('lss', res.data.response);
						uni.setStorageSync('id', res.data.response[0]);
                        uni.showToast({
                            title: `切换为${this.identity}成功`
                        });
                        this.identityCode = res.data.newIdentity; 
                    } else {
                        uni.showToast({
                            title: res.data.message || '切换身份失败',
                            icon: 'none'
                        });
                    }
                },
                fail: () => {
                    uni.showToast({
                        title: '网络错误',
                        icon: 'none'
                    });
                }
            });
        },
        switchToBuyer() {
            this.switchIdentity(1); 
        },
        switchToOwner() {
            this.switchIdentity(5); 
        },
        switchToEmployee() {
            this.switchIdentity(4); 
        }
    }
};
</script>

<style scoped>
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
    background-color: #f3f4f6;
    height: 100vh;
}

.identity-section {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    height: 60px;
}

.label {
    font-size: 16px;
    color: #555;
}

.id {
    font-size: 18px;
    color: #333;
    font-weight: bold;
}

.btn {
    width: 80%;
    height: 45px;
    line-height: 25px;
    font-size: 16px;
    border-radius: 25px;
    text-align: center;
    margin: 10px 0;
    transition: transform 0.3s;
}

.btn:active {
    transform: scale(0.95);
}

.switch-btn {
    background-color: #FF9D6A;
    color: #fff;
}

.switch-btn:hover, .register-btn:hover {
    background-color: #FF6A88; 
}

.register-btn {
    background-color: #FF6A88;
    color: #fff;
}
</style>
